<template>
	<view class="content">
		<view class="titles" v-for="(item,index) in advList" :key="index">
			<view class="title">
				<image class="try" :src="item.cooperationImg"
				 mode=""></image>
			</view>
			<view class="">
				<view class="" style="white-space: nowrap; width: 320rpx; text-align: center; word-break: 	keep-all; overflow: hidden; text-overflow: ellipsis; font-size: 30rpx; color: #333333; font-weight: 900;">{{item.cooperationName}}</view>
			</view>
			<view class="btns" @click="add(item.linkAddr,item.qrcode)">
				点击跳转
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				advList:[] //战略合作列表
			}
		},
		methods: {
			add(a,b) {
				uni.navigateTo({
					url: `../webview/webview?link_add=${a}&img_chat=${b}`
				})
			},
			getzhanlv() { //战略合作伙伴 
			var that=this
			uni.showLoading({
				title:"加载中"
			})
				uni.request({
					url: "http://118.178.89.161:9999/cooperation/query",
					method: 'POST',
					header: {
						"Content-Type": "application/json"
					},
					data: {
						"pageNum": "1",
						"pageSize": "10"
					},
					success(res) {
						if(res.data.status==200) {
							uni.hideLoading()
							that.advList=res.data.data
						}else {
							uni.showToast({
								title:"获取失败",
								icon:'none'
							})
						}
					}
				})
			}
		},
		onLoad() {
			this.getzhanlv()
		}
	}
</script>
<style scoped lang="scss">
	.titles {
		display: flex;
		justify-content: space-around;
		align-items: center;
		margin-top: 38rpx;
		padding-left: 30rpx;
		padding-right: 30rpx;
	}
	.titles:nth-child(1) {
		margin-top: 79rpx;
	}

	.title {
		background-size: 100% 100%;
		width: 142rpx;
		height: 142rpx;
		background-image: url(data:image/png;base64,);
	}

	.try {
		width: 103rpx;
		height: 103rpx;
		border-radius: 50%;
		margin-top: 27rpx;
		margin-left: 16rpx;
	}

	.btns {
		height: 94rpx;
		line-height: 94rpx;
		color: #FFFFFF;
		width: 280rpx;
		text-align: center;
		background-size: 100% 100%;
		background-image: url(data:image/png;base64,);
	}
</style>
